.App {
  background-color: #fff;
  animation:appear 3s;
}

@keyframes appear {
  from {opacity: 0;}
  to {opacity:100;}
} 

.footer {
  min-height:200px;
}

.grid {
  margin-top:25%;
  display:grid;
  max-width:80%;
  margin-left:10%;
}

.item {
  min-width:350px;
  min-height:450px;
  display:block;
  align-items: center;
  align-content: center;
}

.Header {
  position:fixed;
  z-index:3;
}

.grid-image {
  max-height:300px;
  min-width:300px;

  margin-top:20px;
  margin-left:auto;
  margin-right:auto;
}

.lazyload-wrapper {
display:flex;
}


.grid-item {
  display:block;
  margin:auto;
  transition:1s;
}

.grid-item:hover {
  transform:translateY(-20px);
}

@media only screen and (max-width: 600px) {
  .grid {
    grid-template-columns: auto;
  }
}

@media only screen and (min-width: 600px) and (max-width:768px) {
  .grid {
    grid-template-columns: auto;
  }
}

@media only screen and (min-width: 768px) and (max-width:992px) {
  .grid {
    grid-template-columns: auto auto;
  }
}

@media only screen and (min-width: 992px) and (max-width:1200px) {
  .grid {
    grid-template-columns: auto auto;
  }
}

@media only screen and (min-width: 1200px) {
  .grid {
    grid-template-columns: auto auto auto;
  }
}



